import React from 'react'
import "./About.css";

const TextBox = () => {
    return (
        <div className='text-box'>
            <p>Per4mance, nace como una app híbrida, desarrollada mediante el framework de programación web de JavaScript (Lenguaje de programación interpretado, es decir, que ejecuta las instrucciones directamente sin necesidad de compilación en lenguaje máquina), React, que junto a los lenguajes de programación HTML (Lenguaje de Marcas de Hipertexto) y JavaScript nos permite vincular un SGBD (Sistema Gestor de Bases de Datos) no relacional llamado MongoDB,
                para estructurar los datos de los usuarios en cuanto datos personales se refiere e interactuar con la herramienta que podremos acceder mediante un servidor alojado en un hosting.</p>
            <p>La aplicación sirve de apoyo para aquellas personas cuya prioridad es mantener una buena salud teniendo en cuenta sus progresos, en cuanto a objetivos, de la vida diaria como son la pérdida o ganancia de calorías, la cantidad de agua ingerida o la cantidad de horas de sueño realizadas.</p>
            <p>Se podrá disfrutar tanto en dispositivos de alta y baja resolución para deleite del usuario lo que permite a la aplicación una disponibilidad completa en cualquier momento.</p>
            <p>Este proyecto es el deseo del desarrollador de crear una app intuitiva y accesible para gestionar de manera clara su progreso personal sin necesidad de pagar una alta cuantía de dinero por un gestor,
            ya que dispondrá de su app a 'golpe de dos clicks'.</p>
        </div>
    )
}

export default TextBox